<template>
  <div class="product">
    <div v-if="goodsInfo">
      <!--  -->
      <van-swipe :autoplay="5000">
        <van-swipe-item v-for="(item) in bannerLists" :key="item.image_id">
          <img :src="item.image_url" />
        </van-swipe-item>
      </van-swipe>
      <!--  -->
      <div class="goods_header">
        <h1>{{goodsInfo.goods_name}}</h1>
        <span class="price">{{goodsInfo.shop_price}}</span>
        <span class="o_price">:{{goodsInfo.market_price}}</span>
        <p class="shipping">:0</p>
      </div>

      <!--  -->
      <div class="sku-detail adv-opts">
        <div class="sku-detail-inner adv-opts-inner">
          <dl class="sku-group select-sku js-select-sku">
            <dt>
              <span class="js-sku-label"></span>
            </dt>
            <dd class="js-sku-value">
              <span class="sku-item">99950</span>
            </dd>
          </dl>
          <dl class="sku-group select-sku js-select-sku">
            <dt>
              <span class="js-sku-label"></span>
            </dt>
            <dd class="js-sku-value">
              <span class="sku-item"> </span>
            </dd>
          </dl>

          <dl class="sku-group select-sku js-select-sku" is-link @click="openSku">
            <dt>
              <span class="js-sku-label"></span>
            </dt>
            <dd class="js-sku-value">
              <span class="sku-item">1</span>
            </dd>
          </dl>

          <dl class="sku-group select-sku js-select-sku">
            <dt>
              <span class="js-sku-label"></span>
            </dt>
            <dd class="js-sku-value">
              <span class="sku-item"></span>
            </dd>
          </dl>
          <dl class="sku-group select-sku js-select-sku">
            <dt>
              <span class="js-sku-label"></span>
            </dt>
            <dd class="js-sku-value">
              <span class="sku-item"> </span>
            </dd>
          </dl>
        </div>
      </div>

      <!--  -->

      <div class="content" v-html="goodsInfo.goods_content"></div>

      <!--  -->
      <div class="js-bottom-opts js-footer-auto-ele stick-bottom">
        <div class="stick-bottom-icons">
          <a class="icons icons-im"></a>
          <router-link class="icons icons-home" to="/cart"></router-link>
        </div>
        <div class="stick-bottom-btns">
          <div class="btn-cart">
            <a class="js-add-cart" @click="openSku"></a>
          </div>
          <div class="btn-buy">
            <a class="js-buy-it" @click="showsku=true;buy_now=true;"></a>
          </div>
        </div>
      </div>
      <!---->
      <!--addTohapply-->
      <van-popup position="bottom" v-model="showsku">
        <div class="popup">
          <div class="wrap">
            <div class="commodity flex">
              <img :src="goodsInfo.original_img" />
              <div class="middle flex flex_1">
                <p class="title">{{goodsInfo.goods_name}}</p>
                <div class="price">
                  <p class="new">{{goodsInfo.shop_price}}</p>
                  <p class="old">{{goodsInfo.market_price}}</p>
                </div>
              </div>
              <van-icon name="cross" class="cross" @click="showsku=false;buy_now=false" />
            </div>
            <div class="num flex-between cart">
              <p class="title"></p>
              <van-stepper v-model="num" class="flex" />
            </div>
            <div class="count">:{{goodsInfo.store_count}}</div>
          </div>
          <div class="button flex-center" @click="addCart()"></div>
        </div>
      </van-popup>
    </div>

    <div class="loading-more" v-else>
      <span></span>
    </div>
  </div>
</template>

<script>
import { Popup, Swipe, SwipeItem, Stepper, Icon, Toast } from "vant";
import Swiper from "swiper";
import axios from "axios";
import url from "@/axios/api.js";
export default {
  components: {
    "van-popup": Popup,
    "van-swipe": Swipe,
    "van-swipe-item": SwipeItem,
    "van-stepper": Stepper,
    "van-icon": Icon
  },
  name: "Mall",
  data() {
    return {
      buy_now: false,
      showsku: false,
      bannerLists: [],
      goodsInfo: [],
      num: 1,
      item_id: []
    };
  },
  created() {
    this.getData();
    this.init();
  },
  methods: {
    getData() {
      let params = new URLSearchParams();

      params.append("id", this.$route.query.id);
      params.append("openid", localStorage.getItem("openid"));
      axios.post(url.productDetail, params).then(res => {
        this.item_id = res.data.data.filter_spec;
        this.bannerLists = res.data.data.goods_images_list;
        this.goodsInfo = res.data.data.goods;
      });
    },
    init() {
      new Swiper(".swiper-container", {
        pagination: {
          el: ".swiper-pagination"
        },
        autoplay: true,
        loop: true
      });
    },
    openSku() {
      this.showsku = !this.showsku;
    },
    addCart() {
      if (!this.buy_now) {
        let params = new URLSearchParams();
        params.append("openid", localStorage.getItem("openid"));
        params.append("goods_id", this.goodsInfo.goods_id);
        params.append("goods_num", this.num);
        axios.post(url.addCart, params).then(res => {});
        this.$router.push({ path: "/cart" });
      }
      if (this.buy_now) {
        this.$router.push({
          path: "/submitOrder",
          query: {
            action: "buy_now",
            goods_id: this.$route.query.id,
            goods_num: this.num,
            item_id: this.item_id
          }
        });
      }
    }
  }
};
</script>

<style  scoped lang="scss">
@import "./details.scss";
@import "swiper/dist/css/swiper.css";
.content /deep/ p {
  width: 100%;
}

.content /deep/ p /deep/ img {
  width: 100%;
}
.van-swipe {
  height: 375px;
}
.van-swipe img {
  height: 100%;
  width: 100%;
}
.goods_header {
  padding: 10px;
  -webkit-tap-highlight-color: transparent;
  border-top: 1px solid #f2f2f2;
  background-color: #fff;
  margin-bottom: 10px;
}

.goods_header h1 {
  font-size: 18px;
  font-family: SourceHanSansCN-Regular;
  font-weight: 400;
  color: rgba(0, 0, 0, 1);
  line-height: 25px;
  margin-bottom: 10px;
}

.goods_header .price {
  font-size: 19px;
  font-family: SourceHanSansCN-Bold;
  font-weight: bold;
  color: rgba(243, 9, 2, 1);
  line-height: 15px;
}

.goods_header .o_price {
  font-size: 11px;
  font-family: SourceHanSansCN-Regular;
  font-weight: 400;
  text-decoration: line-through;
  color: rgba(130, 130, 130, 1);
  line-height: 15px;
}
.goods_header .shipping {
  font-size: 12px;
  font-family: SourceHanSansCN-Regular;
  font-weight: 400;
  color: rgba(130, 130, 130, 1);
  line-height: 15px;
  margin: 10px 0;
}

/*  */
.sku-detail {
  position: relative;
  margin-top: 10px;
  margin-bottom: 30px;
  line-height: 20px;
  font-size: 14px;
  background-color: #fff;
  border-top: 1px solid #f0f0f0;
  border-bottom: 1px solid #f0f0f0;
}

.sku-detail .sku-detail-inner {
  padding-left: 10px;
}
.sku-detail .sku-group {
  position: relative;
  padding-right: 15px;
  margin-right: 10px;
}
.sku-detail dl {
  padding-top: 13px;
  padding-bottom: 13px;
  zoom: 1;
  border-bottom: 1px solid #aeb5bf;
}

.js-sku-label {
  color: #aeb5bf;
}
.sku-detail dt {
  float: left;
  color: #333;
}
.sku-detail dd {
  margin-left: 42px;
  display: block;
  word-wrap: break-word;
}
.sku-detail .sku-group .sku-item {
  margin-right: 10px;
}
.sku-detail .sku-group.select-sku:after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  margin-top: -6px;
  width: 14px;
  height: 12px;
  background: url(../../assets/images/mall_arrow@2x.png) no-repeat;
  background-size: 100% 100%;
}

/*  */
.stick-bottom {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  font-size: 12px;
  line-height: 16px;
  height: 50px;
  z-index: 100;
  background-color: #fff;
}

.stick-bottom-icons {
  float: left;
  width: 50%;
  font-size: 0;
  border-image: url(https://b.yzcdn.cn/v2/image/wap/border-line-2.png) 2 stretch;
  border-top: 2px solid #e5e5e5;
}
.icons-im {
  background: url(../../assets/images/mall_contact@2x.png) no-repeat;
}
.icons-home {
  background: url(../../assets/images/mall_cart.png) no-repeat;
}
.stick-bottom-icons .icons {
  width: 50%;
  height: 50px;
  display: inline-block;
  line-height: 75px;
  text-align: center;
  font-size: 11px;
  background-repeat: no-repeat;
  background-size: 21px 21px;
  background-position: center 8px;
  color: #828282;
  border-image: url(https://b.yzcdn.cn/v2/image/wap/border-line-2.png) 2 stretch;
  border-right: 2px solid #e5e5e5;
}
.stick-bottom-btns {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 50px;
  line-height: 50px;
}
.stick-bottom-btns .btn-buy,
.stick-bottom-btns .btn-cart {
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  text-align: center;
  padding: 0;
}

.stick-bottom-btns .btn-cart {
  background: #f4b840;
}
.stick-bottom-btns .btn-buy {
  background: #f44;
}
.stick-bottom-btns a {
  color: #fff;
  font-size: 16px;
}
a {
  background: 0 0;
  text-decoration: none;
}

.swiper_mall {
  height: 375px;
}

/*  */
.swiper-container-horizontal
  > .swiper-pagination-bullets
  .swiper-pagination-bullet {
  margin: 0 4px;
}
.swiper-pagination-bullet {
  width: 8px;
  height: 8px;
  display: inline-block;
  border-radius: 100%;
  background: #000;
  opacity: 0.2;
}
.swiper-pagination-bullet-active {
  opacity: 1;
  background: #ff4c23;
}
</style>



